@media only screen and (max-width: 564px) {
  .main2 .content2 .search-content {
    align-items: center;
  }
  .content2
    .search-content
    .search-content-first-side
    .MuiOutlinedInput-adornedStart {
    width: 252px;
  }
  .main2 .content2 .search-content .search-content-buttons {
    width: 107%;
  }
  .App .main2 .content2 .search-content .search-content-buttons .add {
    padding: 8px 7px;
  }
  .content2 .search-content .search-content-buttons .add h2 {
    font-size: 11.7px;
  }
}

@media only screen and (max-width: 850px) {
  .App .main2 .sidebar {
    display: none;
  }
  .App .main2 .content2 {
    width: 100%;
  }
}

@media only screen and (max-width: 883px) {
  .content2 .search-content .search-content-first-side {
    align-items: flex-end;
    flex-direction: column-reverse;
  }
  .content2 .search-content .search-content-first-side .filter {
    display: none;
  }
  .main2 .content2 .search-content .filter-mob {
    display: flex;
  }
  .content2 .search-content .search-content-buttons {
    margin-bottom: 15px;
  }
  .main2 .content2 .search-content .search-content-buttons .add {
    margin-bottom: 0;
    margin-right: 10px;
    padding: 8px 10px;
  }
}

@media only screen and (max-width: 1130px) {
  .main2 .content2 {
    width: 80%;
  }
  .main2 .sidebar {
    width: 20%;
  }
  .content2 .search-content {
    align-items: flex-end;
    flex-direction: column-reverse;
  }
  .content2 .search-content .search-content-buttons .add {
    margin-bottom: 15px;
  }
  .main2 .content2 .footer-side h3 {
    font-size: 12px;
  }
  .main2 .content2 .footer-side p {
    font-size: 10px;
  }
}
